<template>
    <div class="page-cart">
        <div class="columns is-multiline">
            <div class="column is-12">
                <h1 class="title">Cart</h1>

            </div>
            <div class="column is-12 box">
                <table class="table is-fullwidth">
                    <thead>
                        <tr>
                            <th>Card</th>
                            <th>Price</th>
                            <th>Quantity</th>
                            <th>Total</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody v-if="cartTotalLength">
                        <CartItem
                            v-for="item in cart.items"
                            v-bind:key="item.card.id"
                            v-bind:initialItem="item"
                            v-on:removeFromCart="removeFromCart" />
                    </tbody>
                </table>
                <div class="button bottom-right" v-if="cartTotalLength">Check Out</div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import CartItem from '@/components/CartItem.vue'
    export default{
        name: "Cart",
        components: {
            CartItem
        },
        data(){
            return {
                cart: {
                    items:[]
                }
            }
        },
        mounted() {
            this.cart = this.$store.state.cart
        },
        methods: {
            removeFromCart(item){
                console.log("check")
                this.cart.items = this.cart.items.filter(i => i.card.id !== item.card.id)
            }
        },
        computed:{
            cartTotalLength(){
                return this.cart.items.reduce((acc, curVal) => {
                    return acc += curVal.quantity
                }, 0)
            }
        }

    }
</script>