<template>
    <tr>
        <td>
            <router-link :to="item.card.get_absolute_url">{{item.card.name}}</router-link>
        </td>
        <td>
            {{item.quantity}}
        </td>

        <td>
            {{item.card.price}}
        </td>
        <td>
            ${{getItemTotal(item).toFixed(2) }}
        </td>
        <td>
            <button class="delete" @click="remooveFromCart(item)"></button>
        </td>
    </tr>

</template>
<script>
    export default {
        name: "CartItem",
        props: {
            initialItem: Object
        },
        data() {
            return {
                item: this.initialItem
            }
        },
        methods:{
            getItemTotal(item){
                return item.quantity*item.card.price
            },
            updateCart(){
                localStorage.setItem('cart', JSON.stringify(this.$store.state.cart))
            },
            remooveFromCart(item){
                this.$emit("removeFromCart", item)
                this.updateCart()
            }
        },
        
    }
</script>