<template>
    <div class="page-card">
        <div class="columns is-multiline">
            <div class="column is-3">
                <figure class="image mb-6">
                    <img v-bind:src="card.image">
                </figure>
                
            </div>
            <div class="column is-3">
                <h1 class="title">{{card.name}}</h1>
                <p>{{card.description}}</p>
                <p><strong>Price: </strong>{{card.price}}</p>
                <div class="field has-addons mt-6">
                    <div class="control">
                        <input type="number" class="input" min="1" v-model="quantity">
                    </div>
                    <div class="control">
                        <a class="button is-dark" @click = "addToCart">Add to cart</a>
                    </div>
                </div>
            </div>
            <div class="column is-3 drop-header">
                
                <p><strong>Rarity: </strong>{{card.rarity}}</p>
                <p><strong>Total mana cost: </strong>{{card.cmc}}</p>

                <p><strong>Set: </strong>{{card.cardSet}}</p>
                <p><strong>artist: </strong>{{card.artist}}</p>
                
            </div>
        </div>
    </div>
</template>
<style>
    .drop-header{
        padding-top: 4.5rem;
    }
</style>

<script>
    import { toStatement } from "@babel/types"
import axios from "axios"
import { toast } from 'bulma-toast'
    export default{
        name: 'Card',
        data() {
            return {
                card: {},
                quantity: 1,

            }   
        },
        mounted() {
            this.getCard()
        },
        methods:{
            getCard(){
                const card_slug = this.$route.params.card_slug
                axios.get(`/api/v1/card/${card_slug}`)
                .then(response => {
                    this.card = response.data
                })
                .catch(error => {
                    console.log(error+"here?")
                })
            },
            addToCart(){
                if(isNaN(this.quantity) || this.quantity < 1){
                    this.quantity = 1
                }
                const item = {
                    card :this.card,
                    quantity : this.quantity

                }
                this.$store.commit('addToCart', item)

                toast({
                    message: "card added to cart",
                    type: 'is-success',
                    dismissable: true,
                    pauseOnHover: true,
                    duration: 2000,
                    position: 'bottom-right'
                })
            }
        }

    }

</script>