<template>
    <div class="home">
  
      <div class="columns is-multiline">
        <div class="column is-12">
          <h2 class="is-size-2 has-text-centered">Browse Cards</h2>
  
        </div>
        <div class="column is-3" 
          v-for="card in latestCards"
          v-bind:key="card.id">
          <div class="box">
            <figure class="image mb-4">
                <img v-bind:src="card.image">
  
            </figure>
            <h3 class="is-size-4">
              {{card.name}}
            </h3>
            <p class="is-size-6 has-text-grey">${{card.price}}</p>
          </div>
          <router-link v-bind:to="card.get_absolute_url" class="button is-dark mt-4">View details</router-link>
        </div>

      </div>
    </div>
  </template>
  
  <script>
    import axios from 'axios'
  
    export default {
      name: 'Browse',
      data(){
        return {
          latestCards: []
        }
  
      },
      components: {
      },
      mounted(){
        this.getCards()
      },
      methods: {
        getCards(){
            const page = this.$route.params.page

            axios.get(`/api/v1/cards/${page}`)
                .then(response => {
                this.latestCards = response.data
                }).catch(error=>{
                console.log(error)
                })
        }
      }
    }
  </script>
  
  <style scoped>
    .image {
      margin-top: -1.25rem;
      margin-left: -1.25rem;
      margin-right: -1.25rem;
    }
  </style>